@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@400;500;600;700&family=Poppins:ital,wght@0,200;0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@400;500;600;700&family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.2.1/css/all.min.css");
@import url("https://cdn.jsdelivr.net/npm/bootstrap@5.2.3/dist/css/bootstrap.min.css");


:root {
  --light-gray: #F9F9F9;
  --light-gray-101: #FBFBFB;
  --gray-100: #F5F5F5;
  --gray-200: #EFEFEF;
  --gray-250: #F1F1F1;
  --gray-300: #BCBCBC;
  --gray-400: #959595;
  --gray-500: #222;
  --black-100: #222;
  --black-101: #303030;
  --black-102: #404040;
  --green-100: #EFFDF5;
  --green-200: #A5F0C5;
  --green-300: #28B67E;
  --green-400: #00917C;
  --green-500: #035956;
  --green-600: #184D47;
  --green-700: #162F2C;
  --blue: #3448FE;
  --tag-blue: #007bff;
  --fb-blue: #4267B2;
  --google-red: #D34836;
  --red-300: #FF4135;
  --red-400: #D70512;
  --red-500: #FF0022;
  --yellow-star: #FFD800;
  --orange-100: #D53F3F;
  --orange-200: #DC6531;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #303030;
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 1600px;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
}

input {
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
}

textarea {
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
}

select {
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
}

button {
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 28px;
  font-weight: 400;
  margin-bottom: .5rem;
}

h4 {
  font-size: 24px;
  font-weight: 400;
}

h5 {
  font-size: 20px;
  font-weight: 400;
}

h6 {
  font-size: 16px;
  font-weight: 400;
}

p {
  margin-bottom: 1rem;
}

small {
  font-size: 12px;
}

ul {
  list-style: none;
}

hr {
  margin: 1rem 0;
  color: inherit;
  border: 0;
  border-top: 1px solid;
  opacity: .25;
}

::placeholder {
  color: #bcbcbc;
}

.arrow {
  border: solid black;
  border-width: 0 1px 1px 0;
  display: inline-block;
  padding: 3px;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.errmsg {
  color: red;
  font-size: 13px;
}

.fa-star,
.fa-star-half-stroke {
  color: #FFD800;
}

.product-section-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 1600px;
  margin: 0 auto 25px auto;
  padding: 30px 20px 0px 20px;
  border: 1px solid var(--gray-200);
}

.product-section-container .row1,
.product-section-container .row2 {
  margin: 10px 0;
}

.product-section-container .flex .sub-categories-btn {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.product-section-container .flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}


/*-------------------- MEDIA QUERY ------------------------------*/

@media only screen and (max-width: 520px) {

  .product-section-container {
    padding: 30px 8px 0px 8px;
  }
}